.advantages {
  position: relative;
  padding: 100px 0;

  &:before {
    content: url("../images/advantages-decor.svg");
    display: block;
    position: absolute;
    right: 0;
    top: 20%;
    opacity: .1;
  }
}

.advantages__title {
  margin-bottom: 84px;
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  margin: -50px -40px;
}

.advantages__col {
  width: 50%;
  padding: 50px 40px;

  &--center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.advantages__item {
}

.advantages__item-title {
  position: relative;
  margin-bottom: 40px;
  padding-left: 73px;

  font-size: 60px;
  font-weight: 600;
  color: $color-text-secondary;
}

.advantages__value {
  position: absolute;
  left: 0;
  top: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 100px;
  color: #ffffff;

  transform: translateY(-50%);

  &:before {
    content: "";
    position: absolute;
    left: -4px;
    z-index: -1;
    width: 53px;
    height: 53px;

    border: 3px solid $color-text-secondary;
    transform: rotate(-15deg);
  }
}

.advantages__text {
  padding-left: 100px;
  font-size: 20px;
  line-height: 1.45;
  font-weight: 500;
}

.advantages__btn {
  font-size: 32px;
}