// Body
html,
body {
    height: 100%;
    font-family: Gilroy, sans-serif;
    font-size: 20px;
    line-height: 1.2;
    font-weight: 400;
    color: #fff;

    background-color: #222;
}

.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.container {
    margin: 0 auto;
    max-width: 1440px;
    padding: 0 20px;
    box-sizing: content-box;
}
.page {
    flex: 1 1 auto;
}

.section-title {
    font-size: 54px;
    font-weight: 700;
}

