.services {
  position: relative;
  padding: 350px 0 100px;

  &:after {
    content: url("../images/services-decor.svg");
    display: block;
    position: absolute;
    left: 0;
    top: 7%;
    opacity: .1;
  }
}

.services__title {
  margin-bottom: 120px;
}

.services__wrap {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 25px;
}

.services__col {
  margin-right: 155px;

  &:nth-child(3) {
    margin-right: 0;

    .services__icon:after {
      display: none;
    }
  }
}

.services__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.services__info {
  max-width: 270px;
}

.services__item-title {
  margin-bottom: 15px;
  font-size: 26px;
  font-weight: 600;
}

.services__item-text {
  font-size: 19px;
}

.services__icon {
  position: relative;
  width: 250px;
  height: 250px;
  margin-bottom: 35px;

  display: inline-flex;
  align-items: center;
  justify-content: center;

  border-radius: 50%;
  border: 4px solid #ffffff;
  box-shadow: 0 0 50px rgba(#fff, .4), 0 0 50px rgba(#fff, .4) inset;

  &:after {
    content: url('../images/services/line.svg');
    display: block;
    width: 170px;
    height: 30px;
    position: absolute;
    z-index: 1;
    left: 100%;
    top: 50%;

    transform: translateY(-50%);
  }
}

.services__btn {
  text-align: center;
}