/* STYLE ZIRO */
*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 0;
  background: none;
}

a {
  text-decoration: none;
  color: inherit;
}

input, textarea, button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}

ul, li {
  list-style: none;
}

img {
  vertical-align: top;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.eot");
  src: local("Gilroy Regular"), local("Gilroy-Regular"), url("../fonts/Gilroy-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff"), url("../fonts/Gilroy-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Medium.eot");
  src: local("Gilroy Medium"), local("Gilroy-Medium"), url("../fonts/Gilroy-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff"), url("../fonts/Gilroy-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Semibold.eot");
  src: local("Gilroy Semibold"), local("Gilroy-Semibold"), url("../fonts/Gilroy-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Semibold.woff2") format("woff2"), url("../fonts/Gilroy-Semibold.woff") format("woff"), url("../fonts/Gilroy-Semibold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Bold.eot");
  src: local("Gilroy Bold"), local("Gilroy-Bold"), url("../fonts/Gilroy-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff"), url("../fonts/Gilroy-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
html,
body {
  height: 100%;
  font-family: Gilroy, sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-weight: 400;
  color: #fff;
  background-color: #222;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 20px;
  box-sizing: content-box;
}

.page {
  flex: 1 1 auto;
}

.section-title {
  font-size: 54px;
  font-weight: 700;
}

.text-dedicated {
  color: #00FFE0;
}

.btn {
  display: inline-block;
  padding: 0.9em 2em;
  font-family: Gilroy, sans-serif;
  color: #fff;
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  cursor: pointer;
  background-color: #00FFE0;
  box-shadow: 0 2.8951048851px 4.3426570892px 0 rgba(255, 255, 255, 0.4) inset, 0 4px 80px 0 rgba(0, 0, 0, 0.25);
  border-radius: 0 1.5em 0 1.5em;
  border: 1px solid transparent;
  transition: background-color linear 0.2s, boder-color linear 0.2s;
}
.btn:hover {
  background-color: transparent;
  border-color: #00FFE0;
}

.btn--light {
  background-color: rgba(0, 255, 224, 0.5);
  box-shadow: 0 2px 3px 0 rgba(255, 255, 255, 0.4) inset;
}
.btn--light:hover {
  border-color: rgba(0, 255, 224, 0.5);
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(25px);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 150px;
}

.social {
  display: flex;
  align-items: center;
}

.social__link {
  margin-right: 100px;
  font-size: 18px;
  font-weight: 700;
}
.social__link:hover {
  filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.5));
}

.header__phone {
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;
  font-size: 21px;
  font-weight: 700;
}
.header__phone:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 20px;
  height: 20px;
  transform: translateY(-50%);
  background-image: url("../images/tel-icon.svg");
}

.header__btn {
  padding: 0.1em 1.7em;
  font-size: 20px;
  border-radius: 0 0.8em 0 0.8em;
}

.intro {
  position: relative;
  z-index: 1;
}
.intro:before {
  content: url("../images/intro-decor.svg");
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -2;
  filter: drop-shadow(0 0 1px #00ffe0) drop-shadow(0 0 10px #00ffe0) drop-shadow(0 0 20px #00ffe0) drop-shadow(0 0 50px #00ffe0) drop-shadow(0 0 70px #00ffe0) drop-shadow(0 0 150px #00ffe0);
}

.intro__decor {
  position: absolute;
  right: 300px;
  top: 0;
  opacity: 0.1;
}

.intro__bg {
  width: 80vw;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -5;
  overflow: hidden;
}
.intro__bg:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 70%;
  height: 100%;
  background: linear-gradient(to right, #222222 0%, rgba(34, 34, 34, 0) 100%);
}
.intro__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.intro__inner {
  max-width: 600px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 400px 0 450px;
}

.intro__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 60px;
  line-height: 1;
}

.intro__subtitle {
  display: block;
  margin-bottom: 20px;
  font-size: 35px;
  font-weight: 500;
}

.intro__btn {
  display: block;
  box-shadow: 0 2px 3px 0 rgba(255, 255, 255, 0.4) inset;
}

.feedback {
  position: relative;
  z-index: 5;
  background-color: #ffffff;
}

.feedback__bg {
  position: absolute;
  top: -150px;
  left: 0;
  bottom: -200px;
  right: 0;
  z-index: -1;
}
.feedback__bg img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.feedback__decor-one {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

.feedback__decor-two {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

.feedback__inner {
  padding: 100px 0;
}

.feedback__title {
  margin-bottom: 50px;
  text-align: center;
  color: #1A1A1A;
  font-size: 60px;
  font-weight: 600;
}

.feedback__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feedback__input {
  display: block;
  width: 100%;
  max-width: 1000px;
  margin-bottom: 15px;
  padding: 23px 0;
  font-size: 30px;
  text-align: center;
  color: #101111;
  background-color: #EAEAEA;
}
.feedback__input:nth-child(1) {
  border-radius: 40px 40px 0 0;
}
.feedback__input:nth-child(2) {
  margin-bottom: 35px;
  border-radius: 0 0 40px 40px;
}

.feedback__btn {
  font-size: 32px;
  padding: 0.7em 3.6em;
  color: #101111;
}

.services {
  position: relative;
  padding: 350px 0 100px;
}
.services:after {
  content: url("../images/services-decor.svg");
  display: block;
  position: absolute;
  left: 0;
  top: 7%;
  opacity: 0.1;
}

.services__title {
  margin-bottom: 120px;
}

.services__wrap {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 25px;
}

.services__col {
  margin-right: 155px;
}
.services__col:nth-child(3) {
  margin-right: 0;
}
.services__col:nth-child(3) .services__icon:after {
  display: none;
}

.services__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.services__info {
  max-width: 270px;
}

.services__item-title {
  margin-bottom: 15px;
  font-size: 26px;
  font-weight: 600;
}

.services__item-text {
  font-size: 19px;
}

.services__icon {
  position: relative;
  width: 250px;
  height: 250px;
  margin-bottom: 35px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 4px solid #ffffff;
  box-shadow: 0 0 50px rgba(255, 255, 255, 0.4), 0 0 50px rgba(255, 255, 255, 0.4) inset;
}
.services__icon:after {
  content: url("../images/services/line.svg");
  display: block;
  width: 170px;
  height: 30px;
  position: absolute;
  z-index: 1;
  left: 100%;
  top: 50%;
  transform: translateY(-50%);
}

.services__btn {
  text-align: center;
}

.advantages {
  position: relative;
  padding: 100px 0;
}
.advantages:before {
  content: url("../images/advantages-decor.svg");
  display: block;
  position: absolute;
  right: 0;
  top: 20%;
  opacity: 0.1;
}

.advantages__title {
  margin-bottom: 84px;
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  margin: -50px -40px;
}

.advantages__col {
  width: 50%;
  padding: 50px 40px;
}
.advantages__col--center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.advantages__item-title {
  position: relative;
  margin-bottom: 40px;
  padding-left: 73px;
  font-size: 60px;
  font-weight: 600;
  color: #00FFE0;
}

.advantages__value {
  position: absolute;
  left: 0;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 100px;
  color: #ffffff;
  transform: translateY(-50%);
}
.advantages__value:before {
  content: "";
  position: absolute;
  left: -4px;
  z-index: -1;
  width: 53px;
  height: 53px;
  border: 3px solid #00FFE0;
  transform: rotate(-15deg);
}

.advantages__text {
  padding-left: 100px;
  font-size: 20px;
  line-height: 1.45;
  font-weight: 500;
}

.advantages__btn {
  font-size: 32px;
}

.order {
  position: relative;
  padding: 100px 0;
}
.order:after {
  content: url("../images/order-decor.svg");
  display: block;
  position: absolute;
  top: -100px;
  right: 0;
  filter: drop-shadow(0 0 1px #00ffe0) drop-shadow(0 0 10px #00ffe0) drop-shadow(0 0 20px #00ffe0) drop-shadow(0 0 50px #00ffe0) drop-shadow(0 0 70px #00ffe0) drop-shadow(0 0 150px #00ffe0);
}
.order:before {
  content: url("../images/order-decor-two.svg");
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  opacity: 0.1;
}

.order__title {
  margin-bottom: 93px;
}

.order__item {
  margin-bottom: 87px;
  padding-left: 108px;
  position: relative;
}
.order__item:first-child .order__decor {
  border-top: 3px solid #fff;
  border-top-left-radius: 13px;
}
.order__item:first-child .order__decor:after {
  top: -7px;
}
.order__item:last-child {
  margin-bottom: 0;
}
.order__item:last-child .order__decor {
  border-left: none;
  border-bottom: none;
}

.order__decor {
  position: absolute;
  left: 0;
  top: 32px;
  width: 74px;
  height: calc(100% + 98px);
  border-left: 3px solid #fff;
  border-bottom: 3px solid #fff;
  border-radius: 0 0 0 13px;
}
.order__decor:after {
  content: "";
  width: 11px;
  height: 11px;
  display: block;
  position: absolute;
  right: 0;
  top: 4px;
  z-index: 1;
  background-color: #fff;
  transform: rotate(-45deg);
}

.order__item-title {
  margin-bottom: 12px;
  font-size: 60px;
  color: #00FFE0;
  font-weight: 600;
}

.order__text {
  font-size: 20px;
  font-weight: 500;
}

.guarantees {
  padding: 50px 0 100px;
}

.guarantees__title {
  margin-bottom: 140px;
}

.guarantees__wrap {
  display: flex;
  margin: 0 -70px 132px;
}

.guarantees__col {
  width: 25%;
  padding: 0 70px;
}

.guarantees__item {
  text-align: center;
}

.guarantees__icon {
  margin-bottom: 50px;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 206px;
  height: 206px;
}
.guarantees__icon:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  filter: drop-shadow(0px 0px 10px white);
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='white' stroke-width='3' stroke-dasharray='95' stroke-dashoffset='66' stroke-linecap='square'/%3e%3c/svg%3e");
  border-radius: 30px;
}

.guarantees__btn-wrap {
  text-align: center;
}

.works {
  position: relative;
  padding: 100px 0 350px;
}
.works::after {
  content: "";
  display: block;
  height: 150px;
  position: absolute;
  z-index: -1;
  bottom: -5px;
  right: 0;
  left: 0;
  background-image: url("../images/feedback-decor-top.svg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
}

.works__wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: -37px -18px;
}
.works__wrap:before {
  content: url("../images/works/decor-top.svg");
  display: block;
  position: absolute;
  top: -2px;
  left: -22px;
  z-index: 1;
  filter: drop-shadow(0 4px 30px #00ffe0) drop-shadow(0 4px 50px rgba(0, 255, 224, 0.7));
}
.works__wrap:after {
  content: url("../images/works/decor-bottom.svg");
  display: block;
  position: absolute;
  bottom: 36px;
  right: -22px;
  z-index: 1;
  filter: drop-shadow(0 4px 30px #00ffe0) drop-shadow(0 4px 50px rgba(0, 255, 224, 0.7));
}

.works__col {
  width: 33.333%;
  padding: 37px 18px;
}

.works__img {
  margin-bottom: 10px;
  border-radius: 22px;
  overflow: hidden;
}
.works__img img {
  width: 100%;
  max-width: 100%;
  display: block;
  object-fit: cover;
}

.works__name {
  font-size: 40px;
  font-weight: 400;
}

.footer {
  position: relative;
  padding: 300px 0 100px;
}
.footer:after {
  content: url("../images/footer-decor.svg");
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  filter: drop-shadow(0 0 1px #00ffe0) drop-shadow(0 0 10px #00ffe0) drop-shadow(0 0 20px #00ffe0) drop-shadow(0 0 50px #00ffe0) drop-shadow(0 0 70px #00ffe0) drop-shadow(0 0 150px #00ffe0);
}
.footer:before {
  content: "";
  display: block;
  height: 150px;
  position: absolute;
  z-index: -1;
  top: -5px;
  right: 0;
  left: 0;
  background-image: url("../images/feedback-decor-bottom.svg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
}

.footer__inner {
  display: flex;
  justify-content: center;
}

.footer__wrap {
  display: flex;
  max-width: 800px;
}

.footer__list {
  margin-right: 130px;
  width: 50%;
}
.footer__list:last-child {
  margin-right: 0;
}

.footer__item {
  margin-bottom: 26px;
}
.footer__item:last-child {
  margin-bottom: 0;
}

.footer__title {
  font-size: 27px;
  font-weight: 600;
}

.footer__text {
  font-weight: 400;
}