.guarantees {
  padding: 50px 0 100px;
}

.guarantees__title {
  margin-bottom: 140px;
}

.guarantees__wrap {
  display: flex;
  margin: 0 -70px 132px;
}

.guarantees__col {
  width: 25%;
  padding: 0 70px;
}

.guarantees__item {
  text-align: center;
}

.guarantees__icon {
  margin-bottom: 50px;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  width: 206px;
  height: 206px;

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0px 0px 10px rgba(#fff, 1));
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='white' stroke-width='3' stroke-dasharray='95' stroke-dashoffset='66' stroke-linecap='square'/%3e%3c/svg%3e");
    border-radius: 30px;
  }
}

.guarantees__text {

}

.guarantees__btn-wrap {
  text-align: center;
}