.footer {
  position: relative;
  padding: 300px 0 100px;

  &:after {
    content: url("../images/footer-decor.svg");
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;

    filter:
            drop-shadow(0 0 1px rgba($color-text-secondary, 1))
            drop-shadow(0 0 10px rgba($color-text-secondary, 1))
            drop-shadow(0 0 20px rgba($color-text-secondary, 1))
            drop-shadow(0 0 50px rgba($color-text-secondary, 1))
            drop-shadow(0 0 70px rgba($color-text-secondary, 1))
            drop-shadow(0 0 150px rgba($color-text-secondary, 1));
  }

  &:before {
    content: "";
    display: block;
    height: 150px;

    position: absolute;
    z-index: -1;
    top: -5px;
    right: 0;
    left: 0;

    background-image: url("../images/feedback-decor-bottom.svg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
  }
}

.footer__inner {
  display: flex;
  justify-content: center;
}

.footer__wrap {
  display: flex;
  max-width: 800px;
}

.footer__list {
  margin-right: 130px;
  width: 50%;

  &:last-child {
    margin-right: 0;
  }
}

.footer__item {
  margin-bottom: 26px;

  &:last-child {
    margin-bottom: 0;
  }
}

.footer__title {
  font-size: 27px;
  font-weight: 600;
}

.footer__text {
  font-weight: 400;
}