.feedback {
  position: relative;
  z-index: 5;
  background-color: #ffffff;
}

.feedback__bg {
  position: absolute;
  top: -150px;
  left: 0;
  bottom: -200px;
  right: 0;
  z-index: -1;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.feedback__decor-one {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
.feedback__decor-two {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

.feedback__inner {
  padding: 100px 0;
}

.feedback__title {
  margin-bottom: 50px;

  text-align: center;
  color: #1A1A1A;
  font-size: 60px;
  font-weight: 600;
}

.feedback__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feedback__input {
  display: block;
  width: 100%;
  max-width: 1000px;
  margin-bottom: 15px;
  padding: 23px 0;

  font-size: 30px;
  text-align: center;
  color: #101111;

  background-color: #EAEAEA;

  &:nth-child(1) {
    border-radius: 40px 40px 0 0;
  }

  &:nth-child(2) {
    margin-bottom: 35px;
    border-radius: 0 0 40px 40px;
  }
}

.feedback__btn {
  font-size: 32px;
  padding: .7em 3.6em;
  color: #101111;
}