.order {
  position: relative;
  padding: 100px 0;

  &:after {
    content: url("../images/order-decor.svg");
    display: block;
    position: absolute;
    top: -100px;
    right: 0;

    filter:
            drop-shadow(0 0 1px rgba($color-text-secondary, 1))
            drop-shadow(0 0 10px rgba($color-text-secondary, 1))
            drop-shadow(0 0 20px rgba($color-text-secondary, 1))
            drop-shadow(0 0 50px rgba($color-text-secondary, 1))
            drop-shadow(0 0 70px rgba($color-text-secondary, 1))
            drop-shadow(0 0 150px rgba($color-text-secondary, 1));
  }

  &:before {
    content: url("../images/order-decor-two.svg");
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    opacity: .1;
  }
}

.order__title {
  margin-bottom: 93px;
}

.order__item {
  margin-bottom: 87px;
  padding-left: 108px;
  position: relative;

  &:first-child {

    & .order__decor {
      border-top: 3px solid #fff;
      border-top-left-radius: 13px;

      &:after {
        top: -7px;
      }
    }
  }

  &:last-child {
    margin-bottom: 0;

    & .order__decor {
      border-left: none;
      border-bottom: none;
    }
  }
}

.order__decor {
  position: absolute;
  left: 0;
  top: 32px;
  width: 74px;
  height: calc(100% + 98px);

  border-left: 3px solid #fff;
  border-bottom: 3px solid #fff;
  border-radius: 0 0 0 13px;

  &:after {
    content: "";
    width: 11px;
    height: 11px;
    display: block;

    position: absolute;
    right: 0;
    top: 4px;
    z-index: 1;

    background-color: #fff;
    transform: rotate(-45deg);
  }
}

.order__item-title {
  margin-bottom: 12px;

  font-size: 60px;
  color: $color-text-secondary;
  font-weight: 600;
}

.order__text {
  font-size: 20px;
  font-weight: 500;
}