.works {
  position: relative;
  padding: 100px 0 350px;

  &::after {
    content: "";
    display: block;
    height: 150px;

    position: absolute;
    z-index: -1;
    bottom: -5px;
    right: 0;
    left: 0;

    background-image: url("../images/feedback-decor-top.svg");
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
  }
}

.works__wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: -37px -18px;

  &:before {
    content: url("../images/works/decor-top.svg");
    display: block;
    position: absolute;
    top: -2px;
    left: -22px;
    z-index: 1;
    filter: drop-shadow(0 4px 30px rgba($color-text-secondary, 1)) drop-shadow(0 4px 50px rgba($color-text-secondary, .7));
  }
  &:after {
    content: url("../images/works/decor-bottom.svg");
    display: block;
    position: absolute;
    bottom: 36px;
    right: -22px;
    z-index: 1;
    filter: drop-shadow(0 4px 30px rgba($color-text-secondary, 1)) drop-shadow(0 4px 50px rgba($color-text-secondary, .7));
  }
}

.works__col {
  width: 33.333%;
  padding: 37px 18px;
}

.works__img {
  margin-bottom: 10px;
  border-radius: 22px;
  overflow: hidden;

  img {
    width: 100%;
    max-width: 100%;
    display: block;
    object-fit: cover;
  }
}

.works__name {
  font-size: 40px;
  font-weight: 400;
}