.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;

  background-color: rgba(#fff, .1);
  backdrop-filter: blur(25px);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 150px;
}

.social {
  display: flex;
  align-items: center;
}

.social__link {
  margin-right: 100px;
  font-size: 18px;
  font-weight: 700;

  &:hover {
    filter: drop-shadow(5px 5px 10px rgba(#000, .5));
  }
}

.header__phone {
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;

  font-size: 21px;
  font-weight: 700;

  &:before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    width: 20px;
    height: 20px;

    transform: translateY(-50%);
    background-image: url('../images/tel-icon.svg');
  }
}

.header__btn {
  padding: 0.1em 1.7em;

  font-size: 20px;
  border-radius: 0 .8em 0 .8em;

}