.intro {
  position: relative;
  z-index: 1;

  &:before {
    content: url("../images/intro-decor.svg");
    position: absolute;
    left: 0;
    bottom: 0;
    z-index:-2;
    filter:
            drop-shadow(0 0 1px rgba($color-text-secondary, 1))
            drop-shadow(0 0 10px rgba($color-text-secondary, 1))
            drop-shadow(0 0 20px rgba($color-text-secondary, 1))
            drop-shadow(0 0 50px rgba($color-text-secondary, 1))
            drop-shadow(0 0 70px rgba($color-text-secondary, 1))
            drop-shadow(0 0 150px rgba($color-text-secondary, 1));
  }
}

.intro__decor {
  position: absolute;
  right: 300px;
  top: 0;

  opacity: .1;
}

.intro__bg {
  width: 80vw;
  height: 100%;

  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -5;

  overflow: hidden;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 70%;
    height: 100%;

    background: linear-gradient(to right, rgba(#222, 1) 0%, rgba(#222, 0) 100%);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.intro__inner {
  max-width: 600px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 400px 0 450px;
}

.intro__title {
  margin-bottom: 10px;

  font-weight: 700;
  font-size: 60px;
  line-height: 1;
}

.intro__subtitle {
  display: block;
  margin-bottom: 20px;

  font-size: 35px;
  font-weight: 500;
}

.intro__btn {
  display: block;
  box-shadow: 0 2px 3px 0 rgba(255, 255, 255, 0.40) inset;
}