.btn {
  display: inline-block;
  padding: 0.9em 2em;

  font-family: Gilroy, sans-serif;
  color: $white;
  text-align: center;
  font-size: 22px;
  font-weight: 500;

  cursor: pointer;
  background-color: $color-text-secondary;
  box-shadow: 0 2.8951048851013184px 4.342657089233398px 0 rgba(255, 255, 255, 0.40) inset, 0 4px 80px 0 rgba(0, 0, 0, 0.25);
  border-radius: 0 1.5em 0 1.5em;
  border: 1px solid transparent;

  transition: background-color linear .2s, boder-color linear .2s;

  &:hover {
    background-color: transparent;
    border-color: $color-text-secondary;
  }
}

.btn--light {
  background-color: rgba($color-text-secondary, .5);
  box-shadow: 0 2px 3px 0 rgba(255, 255, 255, 0.40) inset;

  &:hover {
    border-color: rgba($color-text-secondary, .5);
  }
}